import { Directive, Input, ElementRef, Renderer, Output, EventEmitter } from "@angular/core";
import { ScrollComponent } from "../scroll/scroll.component";


export class ScrollTopDirective {
  private enable: boolean;
  constructor(private ref: ElementRef) {
    this.enable = !(ref.nativeElement instanceof ScrollComponent);
  }

  
  public scrollChange = new EventEmitter();

  private _scrollTop: number = 0;
  
  public set scrollTop(value: number) {
    if (value == this.ref.nativeElement.scrollTop) return;
    this._scrollTop = value;
    if (!this.enable) return;
    this.ref.nativeElement.scrollTop = value;
    this.scrollChange.emit(this.ref.nativeElement.scrollTop);
  }

  public get scrollTop() {
    return this._scrollTop;
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: "[scrollTop]"
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ElementRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'scrollChange': [{ type: Output },],
'scrollTop': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
